<template>
    <div class="list-main">
      <div class="list-item" v-for="(item,index) in articleList" :key="index">
        <a :href="item.link">
        <div class="list-item-left">
          <p>{{item.title}}</p>
          <span>{{item.created | parseTime('{y}-{m}-{d}')}}</span>
        </div>
        <div class="liet-item-right">
          <img :src="host + item.thumb" alt="">
        </div>
        </a>
      </div>
    </div>
</template>

<script>

export default {
  name: "Teach",
  data() {
    return {
      articleList: [],
      host: ''
    }
  },
    created() {
    this.getArticleList()
  },
  mounted() {
    this.host = document.location.origin
  },
  methods: {
    getArticleList() {
      this.$axios({
        method: 'post',
        url: '/api/article/getList',
        data: {
          "pageNum": 1,
          "pageSize": 100,
          "column": 1,
          "sort": -1
        }
      }).then(res => {
        this.articleList = res.data.data.items
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.list-main {
    margin-top: 15px;
    .list-item {
      height: 90px;
      overflow: hidden;
      margin: 0 15px;
      background-color: #ffffff;
      margin-bottom: 15px;
      border-radius: 6px;
      .list-item-left {
        color: #333333;
        width: calc(100% - 120px);
        float: right;
        position: relative;
        padding:0 10px;
        height: 90px;
        &>p {
          height: 52px;
          overflow: hidden;
          line-height: 26px;
          font-size: 16px;
          margin-top: 8px;
        }
        &>span {
          color: #999999;
          font-size: 14px;
          position: absolute;
          right: 10px;
          bottom: 10px;
        }
      }
      .liet-item-right {
        width: 120px;
        height: 90px;
        float: left;
        &>img {
          width: 100%;
          height: 100%;
          border-top-left-radius: 6px;
          border-bottom-left-radius: 6px;
        }
      }
    }
  }
</style>